.App {
  text-align: center;
}

/* Custom additions for Alumni components */
.btn-primary {
  background: var(--accent, #3b82f6);
  color: white;
  border: 1px solid var(--accent, #3b82f6);
  padding: 0.5rem 1.1rem;
  border-radius: 7px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.18s;
  font-size: 1rem;
  margin-right: 0.5rem;
}
.btn-primary:hover {
  background: #265cac;
  color: #fff;
}

:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: var(--accent);
  --bg-white: #ffffff;
  --bg-gray-50: #f9fafb;
  --bg-gray-100: #f3f4f6;
  --shadow-sm: 0 1px 2px rgba(0,0,0,0.05);
  --shadow-md: 0 4px 6px rgba(0,0,0,0.1);
  --radius-sm: 4px;
  --radius-md: 6px;
  --radius-lg: 8px;
  --radius-xl: 12px;
}

/* form adjustments for component layout */
form[data-testid="alumni-search"] label {
  margin-bottom: 0;
}

.empty-state {
  text-align: center;
  color: var(--text-muted, #9ca3af);
  font-style: italic;
  padding: 2rem;
}

/* Center the pagination nav */
[data-testid="alumni-list"] > div:last-child {
  margin-bottom: 2rem;
}

/* Other existing styles remain */
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
